/*------------------------------------*|
  #CSS custom properties
/*------------------------------------*/
:root {
  --color-white: #ffffff;
  --color-white-opacity: rgba(255, 255, 255, 0.3);
  --color-black: #000000;
  --color-black-opacity: rgba(0, 0, 0, 0.3);
  --color-dark-blue: #283645;
  --color-extra-dark-blue: #1d2631;
  --color-standart-gray: #f2f2f2;
  --color-light-gray: #e5e5e5;
  --color-dark-gray: #283645;
  --color-dark-gray-opacity: rgba(40, 54, 69, 0.3);
  --color-dark-gray-extra-opacity: rgba(40, 54, 69, 0.1);
  --color-pink: #d22856;
  --color-green: #2AAB6D;
  --color-dark-green: #1C9B5E; }

/*------------------------------------*|
  #SCSS-variables
/*------------------------------------*/
/* ------------------------------------*|
  #Базовые стили
/*------------------------------------ */
*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

.page__body {
  margin: 0;
  /* если нет обнуляющего */
  padding: 0;
  overflow-x: hidden;
  color: var(--color-dark-blue);
  font-weight: 400;
  font-size: 14px;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 1.7;
  background: var(--basic-black) url("../img/bg.jpg") top center no-repeat;
  /* всегда для любого текста указывай фон, на случай, если картинка под этим текстом не загрузится */ }

img {
  max-width: 100%;
  /* чтобы картинка не стала больше чем её контейнер */
  height: auto;
  /* картинка сохраняет пропорции */
  vertical-align: top; }

/* ------------------------------------*|
  #Служебные классы
/*------------------------------------ */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
