/* ------------------------------------*|
  #social
/*------------------------------------ */

.social {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.social__item {
  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }
}

.social__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border: 3px solid var(--color-dark-gray-extra-opacity);
  border-radius: 50%;
  transition: border-color 200ms ease 0s;

  &--youtube svg {
    margin-left: 5px;
  }

  &:hover,
  &:focus {
    border-color: var(--color-dark-gray);
  }

  &:active {
    opacity: 0.1;
  }
}

.social__button svg {
  fill: var(--color-pink);
}

/* ------------------------------------*|
  #page-footer
/*------------------------------------ */

.page-footer {
  padding: 40px 0;

  @media (min-width: $desktop-width) {
    padding: 70px 0 75px 0;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.logo-colorful {
  display: block;
  width: 145px;

  &:hover .logo-colorful__text,
  &:focus .logo-colorful__text {
    transition: fill 200ms ease 0s;
    fill: var(--color-pink);
  }

  &:active {
    opacity: 0.3;
  }

  &:active .logo-colorful__text {
    fill: var(--color-dark-blue);
  }
}

.page-footer__logo {
  margin-bottom: 45px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.page-footer__social {
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
}

.copyright__text {
  margin-right: 15px;
  font-size: 14px;
  line-height: 18px;
}

.copyright a {
  display: flex;
  align-items: center;
}

.copyright__svg-icon {
  transition: fill 200ms ease 0s;
  fill: var(--color-pink);

  &:hover,
  &:focus {
    fill: var(--color-dark-blue);
  }

  &:active {
    opacity: 0.3;
    fill: var(--color-dark-blue);
  }
}
